import DateFnsUtils from "@date-io/date-fns";
import { Grid, Typography } from "@material-ui/core";
import Button from "@material-ui/core/Button";
import {
  KeyboardDatePicker,
  MuiPickersUtilsProvider,
} from "@material-ui/pickers";
import MaterialTable from "material-table";
import React, { useState } from "react";
import { useSelector } from "react-redux";
import { Redirect } from "react-router-dom";
import PaymentIcon from "@material-ui/icons/Payment";
import messages from "../../helpers/messages";
import {
  labPaid,
  applicationsByDates,
} from "../../redux/actions/applications_actions";
import {postHaggana} from "../../redux/actions/credits_actions";

export default function WalletTransactionsDate(props) {
  const role = useSelector((store) => store.authReducer.role);

  const [applications, setApplications] = useState([]);
  const [paymentsDetails, setPaymentsDetails] = useState({
    mazin: "",
    moniem: "",
    lab: "",
    labDebit: "",
  });
  const [dates, setDates] = useState({
    startDate: new Date(),
    endDate: new Date(),
  });

  const handleStartDateChange = (startDate) => {
    setDates({ ...dates, startDate });
  };

  const handleEndDateChange = (endDate) => {
    setDates({ ...dates, endDate });
  };

  const handleDateChange = async () => {
    const result = await applicationsByDates(dates);
    if (result) {
      setPaymentsDetails({
        lab: result.lab,
        moniem: result.moniem,
        mazin: result.mazin,
        labDebit: result.labDebit,
      });
      result.data.forEach((application) => {
        switch (application.state) {
          case "registered":
            application.state = " ";
            break;

          case "tested":
            application.state = " ";
            break;

          case "result issued":
            application.state = "  ";
            break;

          case "result delivered":
            application.state = " ";
            break;
          default:
            break;
        }

        switch (application.type) {
          case "internal":
            application.type = " ";
            break;

          case "external":
            application.type = " ";
            break;
          default:
            break;
        }
      });
      setApplications(
        result.data.map((app) => ({
          ...app,
          flightDate: new Date(app.flightDate).toLocaleDateString(),
          testDate: new Date(app.testDate).toLocaleDateString(),
        }))
      );
    }
  };

  const columns = [
    {
      title: "",
      field: "ename",
    },
    {
      title: "",
      field: "fees",
    },
    {
      title: " ",
      field: "type",
    },
    {
      title: " ",
      field: "phoneNumber",
    },
    {
      title: " ",
      field: "passportNumber",
    },
    {
      title: "",
      field: "destination",
    },
    {
      title: " ",
      field: "flightDate",
    },
    {
      title: " ",
      field: "testDate",
    },
  ];

  const handleLabPaid = {
    icon: () => <PaymentIcon />,
    tooltip: "    ",
    onClick: async (event, data) => {
      if (role === "admin" || role === "super admin") {
        const paymentsSucceeded = await labPaid(data);
        if (paymentsSucceeded) {
          const haggana = {...paymentsDetails, date: new Date(), done: false};
          const result = await postHaggana(haggana);
          if (result)
          { messages.success("    ");
          setApplications([]);}
        }
      }
    },
  };

  return role === "admin" || role === "super admin" ? (
    <React.Fragment>
      <div style={{ margin: "20px" }}>
        <Grid
          container
          direction="row"
          justify="center"
          alignItems="center"
          spacing={3}
        >
          <Grid item xs={12}>
            <Typography variant="h3" gutterBottom align="center">
                
            </Typography>
            <div style={{ width: "1200px", marginRight: "50px" }}>
              <Grid
                container
                direction="row"
                justify="center"
                alignItems="center"
                spacing={3}
                xs={12}
              >
                <Grid item xs={4}>
                  <MuiPickersUtilsProvider utils={DateFnsUtils}>
                    <div
                      style={{
                        display: "flex",
                        alignItems: "center",
                      }}
                    >
                      <KeyboardDatePicker
                        variant="inline"
                        format="MM/dd/yyyy"
                        margin="normal"
                        id="date-picker-inline"
                        disableFuture
                        label=" "
                        value={dates.startDate}
                        onChange={handleStartDateChange}
                        KeyboardButtonProps={{
                          "aria-label": "change date",
                        }}
                      />
                      <span
                        style={{
                          fontSize: 12,
                          marginRight: 20,
                          marginTop: 35,
                          color: "gray",
                        }}
                      >
                               
                      </span>
                    </div>
                  </MuiPickersUtilsProvider>
                </Grid>
                <Grid item xs={4}>
                  <MuiPickersUtilsProvider utils={DateFnsUtils}>
                    <div
                      style={{
                        display: "flex",
                        alignItems: "center",
                      }}
                    >
                      <KeyboardDatePicker
                        variant="inline"
                        format="MM/dd/yyyy"
                        margin="normal"
                        label=" "
                        value={dates.endDate}
                        onChange={handleEndDateChange}
                        name="passportExpirateDate"
                        KeyboardButtonProps={{
                          "aria-label": "change date",
                        }}
                      />
                      <span
                        style={{
                          fontSize: 12,
                          marginRight: 20,
                          marginTop: 35,
                          color: "gray",
                        }}
                      >
                            
                      </span>
                    </div>
                  </MuiPickersUtilsProvider>
                </Grid>

                <Grid
                  //   container
                  //   direction="row"
                  //   justify="center"
                  //   alignItems="center"
                  //   spacing={3}
                  item
                  xs={4}
                >
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={handleDateChange}
                  >
                      
                  </Button>
                </Grid>
              </Grid>
            </div>
          </Grid>
          <Grid
            container
            direction="row"
            justify="center"
            alignItems="center"
            spacing={3}
          >
            <Grid item xs={6}>
              <Typography variant="h6" gutterBottom align="center">
                  :{" "}
                {paymentsDetails.lab ? paymentsDetails.lab : ""}
              </Typography>
            </Grid>

            {/* <Grid item xs={6}>
              <Typography variant="h6" gutterBottom align="center">
                   :{" "}
                {paymentsDetails.labDebit ? paymentsDetails.labDebit : ""}
              </Typography>
            </Grid> */}

            <Grid item xs={6}>
              <Typography variant="h6" gutterBottom align="center">
                   :{" "}
                {paymentsDetails.moniem ? paymentsDetails.moniem : ""}
              </Typography>
            </Grid>

            {/* <Grid item xs={6}>
              <Typography variant="h6" gutterBottom align="center">
                   :{" "}
                {paymentsDetails.mazin ? paymentsDetails.mazin : ""}
              </Typography>
            </Grid> */}
          </Grid>
          <Grid xs={12}>
            <div style={{ margin: "50px" }}>
              <MaterialTable
                title="  "
                columns={columns}
                data={applications}
                options={{
                  rowStyle: {
                    backgroundColor: "#EEE",
                  },
                  exportButton: { csv: true },
                  grouping: true,
                  selection: true,
                  pageSizeOptions: [5, 10, 20, 40, 100, 200, 500, 1000, 10000],
                  actionsColumnIndex: -1,
                }}
                localization={{
                  toolbar: {
                    searchPlaceholder: "",
                    searchTooltip: "",
                    exportTitle: " ",
                    exportName: " ",
                    exportAriaLabel: " ",
                  },
                  pagination: {
                    labelDisplayedRows: " {from}  {to}    {count}",
                    lastTooltip: "",
                    firstTooltip: "",
                    previousTooltip: "",
                    nextTooltip: "",
                    labelRowsSelect: "",
                  },
                  header: {
                    actions: "",
                  },
                  body: {
                    emptyDataSourceMessage: "   ",
                    filterRow: {
                      filterTooltip: "",
                    },
                    deleteTooltip: "",
                    editTooltip: "",
                    addTooltip: "",
                    editRow: {
                      deleteText: "     ",
                      cancelTooltip: " ",
                      saveTooltip: " ",
                    },
                  },
                  grouping: {
                    placeholder:
                      "           (         )",
                    groupedBy: "  :",
                  },
                }}
                actions={
                  ["admin", "super admin"].includes(role) && [handleLabPaid]
                }
              />
            </div>
          </Grid>
        </Grid>
      </div>
    </React.Fragment>
  ) : (
    <Redirect to="/dashboard" />
  );
}
