import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import InputLabel from "@material-ui/core/InputLabel";
import Typography from "@material-ui/core/Typography";
import {
  KeyboardDatePicker,
  MuiPickersUtilsProvider,
} from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";
import React from "react";

const useStyles = makeStyles((theme) => ({
  texField: {
    width: "65%",
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 180,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
  container: {
    display: "flex",
    flexWrap: "wrap",
  },
  textTimeField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: 200,
  },
}));

export default function GeneralInfoForm({
  application,
  errors,
  onChange,
  onFlightDateChange,
  onTestDateChange,
}) {
  const classes = useStyles();
  return (
    <React.Fragment>
      <Typography variant="h6" gutterBottom>
          
      </Typography>
      <Grid container spacing={3}>  
        <Grid item xs={12}>
          <TextField
            className={classes.texField}
            name="ename"
            error={errors.ename}
            label="  "
            onChange={onChange}
            value={application.ename}
          />
        </Grid>
        <Grid dir="rtl" item xs={12}>
          <Grid dir="rtl" item xs={6}>
            <FormControl className={classes.formControl}>
              <InputLabel id="type"> </InputLabel>
              <Select
                labelId="type"
                name="type"
                error={errors.type}
                value={application.type}
                onChange={onChange}
              >
                <MenuItem value="internal"> </MenuItem>
                <MenuItem value="external"> </MenuItem>
              </Select>
            </FormControl>
          </Grid>
          <Grid dir="rtl" item xs={6}>
            <small>   -  </small>
          </Grid>
        </Grid>
        {application.type === "external" && (
          <Grid item xs={12}>
            <TextField
              className={classes.texField}
              name="location"
              label="  "
              onChange={onChange}
              value={application.location}
            />
          </Grid>
        )}
        <Grid item xs={12} sm={6}>
          <TextField
            className={classes.texField}
            name="destination"
            error={errors.destination}
            label=" "
            value={application.destination}
            onChange={onChange}
          />
        </Grid>
        <Grid dir="rtl" item xs={12}>
          <FormControl className={classes.formControl}>
            <InputLabel id="airlines"> </InputLabel>
            <Select
              labelId="airlines"
              name="airlines"
              value={application.airlines}
              onChange={onChange}
            >
              <MenuItem value="Sudanair"> </MenuItem>
              <MenuItem value="Badr"></MenuItem>
              <MenuItem value="Tarko"></MenuItem>
              <MenuItem value="Eithiopian"></MenuItem>
              <MenuItem value="Turkey"></MenuItem>
              <MenuItem value="Fly Dubai"> </MenuItem>
              <MenuItem value="Qatar"></MenuItem>
              {/* <MenuItem value="Fly Emarits"></MenuItem> */}
              <MenuItem value="Itihad"></MenuItem>
              <MenuItem value="Nas">  </MenuItem>
              <MenuItem value="Saudi"></MenuItem>
              <MenuItem value="other"></MenuItem>
            </Select>
          </FormControl>
        </Grid>
        {application.airlines === "other" && (
          <Grid item xs={12} sm={12}>
            <TextField
              className={classes.texField}
              name="otherAirlines"
              error={errors.otherAirlines}
              label=" "
              onChange={onChange}
              value={application.otherAirlines}
            />
          </Grid>
        )}
        <Grid item xs={6} sm={12}>
          <TextField
            className={classes.texField}
            name="phoneNumber"
            error={errors.phoneNumber}
            label=" "
            onChange={onChange}
            value={application.phoneNumber}
          />
          <Grid item xs={6} sm={12}>
            <div>
            <small>
                     , : 9123000000
            </small>
            </div>
            <div>
            <small>    </small>
            </div>
            <div>
            <small>     9 </small>
            </div>
          </Grid>
        </Grid>
        <Grid item xs={12} sm={12}>
          <TextField
            className={classes.texField}
            name="passportNumber"
            error={errors.passportNumber}
            label=" "
            onChange={onChange}
            value={application.passportNumber}
          />
        </Grid>

        <Grid item xs={12}>
          <Grid item xs={6}>
            <MuiPickersUtilsProvider utils={DateFnsUtils}>
              <div
                style={{
                  display: "flex",
                  alignItems: "center",
                }}
              >
                <KeyboardDatePicker
                  variant="inline"
                  format="dd/MM/yyyy"
                  margin="normal"
                  id="date-picker-inline"
                  disablePast
                  label=" "
                  value={application.flightDate}
                  onChange={onFlightDateChange}
                  KeyboardButtonProps={{
                    "aria-label": "change date",
                  }}
                />
              </div>
            </MuiPickersUtilsProvider>
          </Grid>

          <Grid item xs={6}>
            <small>    </small>
          </Grid>
        </Grid>
        <Grid>
          <form className={classes.container} noValidate>
            <TextField
              id="time"
              name="flightTime"
              label="  "
              type="time"
              defaultValue="12:00"
              className={classes.textTimeField}
              onChange={onChange}
              error={errors.flightTime}
              value={application.flightTime}
              InputLabelProps={{
                shrink: true,
              }}
              inputProps={{
                step: 300, // 5 min
              }}
            />
          </form>
        </Grid>
        <Grid item xs={12}>
          <Grid item xs={6}>
            <MuiPickersUtilsProvider utils={DateFnsUtils}>
              <div
                style={{
                  display: "flex",
                  alignItems: "center",
                }}
              >
                <KeyboardDatePicker
                  variant="inline"
                  format="dd/MM/yyyy"
                  margin="normal"
                  id="date-picker-inline"
                  disablePast
                  label=" "
                  value={application.testDate}
                  onChange={onTestDateChange}
                  KeyboardButtonProps={{
                    "aria-label": "change date",
                  }}
                />
              </div>
            </MuiPickersUtilsProvider>
          </Grid>
          <Grid>
            <small>
                      24    
            </small>
          </Grid>
        </Grid>
      </Grid>
    </React.Fragment>
  );
}
