import OpenIcon from "@material-ui/icons/Launch";
import MaterialTable from "material-table";
import { makeStyles } from "@material-ui/core/styles";
import React, { useEffect, useState } from "react";
import { Redirect } from "react-router-dom";
import { getHagganas, doneHaggana } from "../../redux/actions/credits_actions";
import messages from "../../helpers/messages";
import { useSelector } from "react-redux";


const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
  layout: {
    width: "auto",
    marginLeft: theme.spacing(2),
    marginRight: theme.spacing(2),
    [theme.breakpoints.up(800 + theme.spacing(2) * 2)]: {
      width: "80vw",
      marginLeft: "auto",
      marginRight: "auto",
    },
  },
  paper: {
    marginBottom: theme.spacing(3),
    padding: theme.spacing(2),
    [theme.breakpoints.up(800 + theme.spacing(3) * 2)]: {
      marginBottom: theme.spacing(6),
      padding: theme.spacing(2),
    },
  },
  buttons: {
    display: "flex",
    justifyContent: "flex-end",
  },
  button: {
    marginTop: theme.spacing(3),
    marginLeft: theme.spacing(1),
  },
  texField: {
    marginTop: 15,
    marginBottom: 15,
  },
  select: {
    minWidth: 185,
    marginTop: 15,
    marginBottom: 15,
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 180,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
  autoComplete: {
    minWidth: 250,
    marginTop: 15,
    marginBottom: 15,
  },
  indicator: {
    backgroundColor: "white",
    height: "5px",
  },
  modal: {
    width: "100%",
    height: "100%",
  },
}));

const columns = [
  {
    title: "",
    field: "lab",
  },
  {
    title: "",
    field: "mazin",
  },
  {
    title: "",
    field: "moniem",
  },
  {
    title: "",
    field: "date",
  },
  {
    title: "",
    field: "done",
  },
];

export default function Applications(props) {
  const classes = useStyles();
  const role = useSelector((store) => store.authReducer.role);

  const [hagganas, setHagganas] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      const result = await getHagganas();
      if (result) {
        setHagganas(result.map(hagg => ({...hagg, date: new Date(hagg.date).toLocaleDateString(), done: hagg.done? "" : " "})));
      }
    };
    fetchData();
  }, []);

  return role === "admin" || role === "super admin" ? (
    <div className={classes.layout} style={{ margin: "50px", width: "75vw" }}>
      <MaterialTable
        title=""
        data={hagganas}
        columns={columns}
        options={{
          rowStyle: {
            backgroundColor: "#EEE",
          },
          exportButton: { csv: true },
          grouping: true,
          pageSizeOptions: [5, 10, 20, 40, 100, 200, 500, 1000, 10000],
          actionsColumnIndex: -1,
        }}
        localization={{
          toolbar: {
            searchPlaceholder: "",
            searchTooltip: "",
            exportTitle: " ",
            exportName: " ",
            exportAriaLabel: " ",
          },
          pagination: {
            labelDisplayedRows: " {from}  {to}    {count}",
            lastTooltip: "",
            firstTooltip: "",
            previousTooltip: "",
            nextTooltip: "",
            labelRowsSelect: "",
          },
          header: {
            actions: "",
          },
          body: {
            emptyDataSourceMessage: "     ",
            filterRow: {
              filterTooltip: "",
            },
            deleteTooltip: "",
            editTooltip: "",
            addTooltip: "",
            editRow: {
              deleteText: "     ",
              cancelTooltip: " ",
              saveTooltip: " ",
            },
          },
          grouping: {
            placeholder:
              "           (         )",
            groupedBy: "  :",
          },
        }}
        actions={[
          {
            icon: () => <OpenIcon />,
            tooltip: "",
            onClick: async (event, data) => {
              if (role === "super admin") {
                const result = await doneHaggana(data._id);
                if (result) {
                  setHagganas(
                    hagganas.map((hagg) =>
                      hagg._id === data._id
                        ? { ...hagg, done: "" }
                        : { ...hagg, done: " " }
                    )
                  );
                  messages.success("  ");
                }
              } else messages.error("  ");
            },
          },
        ]}
      />
    </div>
  ) : (
    <Redirect to="/dashboard" />
  );
}
