import OpenIcon from "@material-ui/icons/Launch";
import MaterialTable from "material-table";
import React, { useEffect, useState } from "react";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import Button from "@material-ui/core/Button";
import DialogTitle from "@material-ui/core/DialogTitle";
import { useSelector } from "react-redux";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import { useHistory, Redirect } from "react-router-dom";
import { getUsers, deleteUser } from "../../redux/actions/users_actions";
import WhatsAppIcon from "@material-ui/icons/WhatsApp";
import { set } from "date-fns";


const columns = [
  {
    title: " ",
    field: "userName",
  },
  {
    title: " ",
    field: "name",
  },
  {
    title: " ",
    field: "ownerName",
  },
  {
    title: "",
    field: "name",
  },
  {
    title: " ",
    field: "phoneNumber",
  },
  {
    title: " ",
    field: "type",
  },
];

const getType = (type) => {
  switch (type) {
    case "corporate":
      type = "";
      break;
    case "organization":
      type = "";
      break;
    case "agency":
      type = "";
      break;
    case "diplomatic committee":
      type = " ";
      break;
      case "recruitment office":
      type = " ";
      break;
      case "individuals":
      type = "";
      break;
      case "other":
      type = "";
      break;
    default:
      break;
  }
};

const getRole = (role) => {
  switch (role) {
    case "admin":
      role = "";
      break;
    case "lab":
      role = " ";
      break;
    case "agency":
      role = "";
      break;
    case "user":
      role = "";
      break;
    default:
      break;
  }
};

export default function Users(props) {
  const history = useHistory();
  const role = useSelector((store) => store.authReducer.role);

  const [users, setUsers] = useState([]);
  const [message, setMessage] = useState({
    text: ""
  });
  const [userPhoneNumber, setUserPhoneNumber] = useState("");
  const [whatsappModal, setWhatsappModal] = useState(false);

  const handleMessageChange = (e) => {
setMessage({[e.target.name]: e.target.value});
  }

  const handleCloseMessageModal = () => {
setWhatsappModal(false)
  }

  useEffect(() => {
    const fetchData = async () => {
      if (role === "admin" || role === "super admin") {
        const result = await getUsers();
      if(result)
      
      {
        result.forEach(u => {
    switch (u.type) {
    case "corporate":
      u.type = "";
      break;
    case "organization":
      u.type = "";
      break;
    case "agency":
      u.type = "";
      break;
    case "diplomatic committee":
      u.type = " ";
      break;
      case "recruitment office":
      u.type = " ";
      break;
      case "individuals":
      u.type = "";
      break;
      case "other":
      u.type = "";
      break;
    default:
      break;
  }
        })
        const users = result.map((user) => ({
          ...user,
          role: getRole(user.role),
        }));
        setUsers(users);
      }}
    };
    fetchData();
  }, [role]);

  return role === "admin" || role === "super admin" ? (
    <div style={{ margin: "50px", width: "75vw" }}>
      <Dialog open={whatsappModal} onClose={handleCloseMessageModal}>
        <DialogTitle>   </DialogTitle>
        <DialogContent>
          <Grid container spacing={5}>
            <Grid dir="rtl" item xs={12}>
              <TextField
                label=""
                dir="rtl"
                name="text"
                onChange={handleMessageChange}
                value={message.text}
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions style={{ margin: "auto" }}>
          <Button onClick={handleCloseMessageModal} color="primary">
            
          </Button>
          <Button
            onClick={() =>
              window.open(
                `https://api.whatsapp.com/send?phone=249${userPhoneNumber}&text=${message.text}`,
                "_blank"
              )
            }
            color="secondary"
          >
             
          </Button>
        </DialogActions>
      </Dialog>

      <MaterialTable
        title=""
        data={users}
        columns={columns}
        options={{
          exportButton: { csv: true },
          grouping: true,
          // selection: true,
          rowStyle: {
            backgroundColor: "#EEE",
          },
        }}
        localization={{
          toolbar: {
            searchPlaceholder: "",
            searchTooltip: "",
            exportTitle: " ",
            exportName: " ",
          },
          pagination: {
            labelDisplayedRows: " {from}  {to}    {count}",
            lastTooltip: "",
            firstTooltip: "",
            previousTooltip: "",
            nextTooltip: "",
            labelRowsSelect: "",
          },
          header: {
            actions: "",
          },
          body: {
            emptyDataSourceMessage: "  ",
            filterRow: {
              filterTooltip: "",
            },
            deleteTooltip: "",
            editTooltip: "",
          },
          grouping: {
            placeholder:
              "          ",
          },
        }}
        actions={[
          {
            icon: () => <OpenIcon></OpenIcon>,
            tooltip: "  ",
            onClick: (event, data) => {
              history.push(`/user/${data._id}`);
            },
          },
          {
            icon: () => <WhatsAppIcon></WhatsAppIcon>,
            tooltip: "  ",
            onClick: (event, data) => {
              setUserPhoneNumber(data.phoneNumber);
              setWhatsappModal(true);
            },
          },
          {
            icon: "delete",
            tooltip: " ",
            disabled: true,
            onClick: async (event, data) => {
              if (role === "super admin") {
                await deleteUser(data._id).catch(() => {
                  return;
                });
                setUsers(users.filter((user) => user._id !== data._id));
              }
            },
          },
        ]}
      />
    </div>
  ) : (
    <Redirect to="/dashboard" />
  );
}
